@import url(https://fonts.googleapis.com/css?family=Do+Hyeon&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto+Condensed&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body{
  background-color: gray;
}

a{
  text-decoration: none;
}

.main{
  margin-top: 50px;
  padding-top: 10px;
}

.main .main-wrapper{
  margin-top: 50px;
}
#root{
  height: 100vh;
  background-color: rgb(245, 245, 245);
}
.topMenu{
    width: 100%;
}

.title-logo{
    width: auto; height: auto;
    max-width: 100px;
    max-height: 100px;
}

.header-link-box{
    margin-right: 40px;
}
.header-link-box a{
    font-size: 18px;
    color : rgb(199, 199, 199);
    text-decoration: none;
}
.header-link-box a:hover{
    color : white;
    text-decoration: none;
}
.header-link-box a:active{
    color : white;
    text-decoration: none;
}

.header-logo-box{
    margin-right: 20px;
    margin-top: 5px;
}

.header-logo-box img{
    width: 20px;
    height: 20px;
}



.pc-item-wrapper{
    height: auto;
    margin-left: 10px;
    margin-right: 10px;
    width: 100%;
    background-color: white;
    margin-bottom: 10px;
    background-color:white;
    border: gray;
    border-bottom: 1px;
    padding-top: 10px;
    padding-left: 5px;
    padding-bottom: 5px;
    padding-right: 5px;
    border-radius: 3px;
    box-shadow: 0 1px 3px rgba(189, 189, 189, 0.16), 0 3px 6px rgba(187, 187, 187, 0.23);
}
button{
    border: 0;
    width: 100%;
}

.pc-item-wrapper .id-span{
    font-size: 20px;
    font-family: 'Roboto Condensed', sans-serif;
}

.pc-item-wrapper .update-log{
    margin-left: 10px;
    font-size: 11px;
    color:gray
}

.pc-item-wrapper .pc-off-alert-box{
    padding-left: 10px;
    padding-right: 10px;
}
.pc-item-wrapper .id-box p{
    font-size: x-large;
}

.pc-item-wrapper .on-light{
    float: left;
    height: 100%;
    width: 2px;
    margin-right: 10px;
    background-color: blue;
}

.pc-item-wrapper .off-light{
    float: left;
    height: 100%;
    width: 2px;
    margin-right: 10px;
    background-color: gray;
}

.pc-item-wrapper .expand-button{
    height:30px;
    float: right;
    font-size: 12px;
    border: 0;
    color: rgb(54, 54, 54);
    width: 30%;
    background-color: rgb(199, 199, 199);
}

 
.pc-item-wrapper .info-text-box{
    color:gray;
    text-align: center;
    font-size: 12px;
}

.pc-item-wrapper .value-text-box{
    width: 100%;
    text-align: center;
    font-size: 20px;
    margin-top: -15px;
    font-family: 'Roboto Condensed', sans-serif;
}

.pc-item-wrapper .collapse-item-box{
    overflow: hidden;
}

.pc-item-wrapper .collapse-item-wrapper{
    margin-bottom: 20px;

}

.pc-item-wrapper .usage-over-90{
    color: rgb(236, 104, 104);
    font-size: 10px;
    text-align: center;
}
.pc-item-wrapper .usage-over-70{
    color: rgb(238, 133, 92);
    font-size: 10px;
    text-align: center;
}
.pc-item-wrapper .usage-over-50{
    color: rgb(126, 126, 126);
    font-size: 10px;
    text-align: center;
}
.pc-item-wrapper .usage-over-30{
    color: rgb(144, 144, 144);
    font-size: 10px;
    text-align: center;
}
.pc-item-wrapper .usage-over-10{
    color: rgb(52, 55, 240);
    font-size: 10px;
    text-align: center;
}
.pc-item-wrapper .usage-over-0{
    color: rgb(141, 141, 141);
    font-size: 10px;
    text-align: center;
}

.pc-item-wrapper .data-progress-box{
    margin-bottom: 10px;
}
.scroll {
    opacity: 0.3;
    background-color: black;
    width: 60px;
    height: 60px;
    position: fixed;
    bottom: 10px;
    right: 10px;
    border-radius: 30px;
    border: gray;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.16), 0 6px 9px rgba(0, 0, 0, 0.23);
    z-index: 500;
  }
  .scroll:hover {
    opacity: 1;
  }
  
  .arrow-up {
    font-family: 'Roboto Condensed', sans-serif;
    color: white;
    font-size: 15px;
   }
  
.polling-state-wrapper{
    height: 30px;
    padding:5px;
}

.polling-state-wrapper .text-box{
    float: left;
}

.polling-state-wrapper .spinner-box{
    float: right;
}

.login-wrapper{
    height: auto;
    width: 100%;
    background-color: white;
    margin-bottom: 10px;
    background-color:white;
    border: gray;
    border-bottom: 1px;
    padding-top: 10px;
    padding-left: 5px;
    padding-bottom: 5px;
    padding-right: 5px;
    border-radius: 3px;
    box-shadow: 0 1px 3px rgba(189, 189, 189, 0.16), 0 3px 6px rgba(187, 187, 187, 0.23);
}

.login-wrapper .header-box {
    margin-top: 25px;
    text-align: center;
}
.login-wrapper .header-box .header{
    font-size: 28px;
    font-family: 'Roboto Condensed', sans-serif;
}

.login-wrapper .copylight{
    font-family: 'Roboto Condensed', sans-serif;
    color: gray;
}

.login-wrapper .input-box{
    margin-top: 45px;
    width: 100%;
    text-align: center;
    height: auto;
}

.login-wrapper .input-box .input-box-sizing{
    display: inline-block;
    width: 70%;
    height: auto;
}

.login-wrapper .input-box .input-box-sizing div{
    width: 100%;
}

.login-wrapper .pw-box{
    margin-top: 20px;
}

.submit-box{
    margin-top: 25px;
    width: 100%;   
    text-align: center;
}

.submit-box .button-box{
    display: inline-block;
    width: 70%;
}

.submit-box .help-box{
    margin-top: 25px;
    margin-bottom: 25px;
}
